.book-loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.book {
  position: relative;
  height: 20px;
  width: 1px;
  background: #999;
}

.book .inner {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #999;
}

.book .inner .left,
.book .inner .middle,
.book .inner .right {
  position: absolute;
  height: 100%;
  background: #999;
}

.book .inner .left {
  width: 20%;
  left: -20%;
}

.book .inner .middle {
  width: 60%;
  left: 20%;
}

.book .inner .right {
  width: 20%;
  left: 80%;
}

.book ul {
  position: relative;
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  margin: 0;
  height: 5px;
  width: 60%;
  left: 20%;
  top: 50%;
}

.book ul li {
  height: 100%;
  width: 10%;
  background: #999;
}

.button-file-upload {
  display: inline-block;
  padding: 10px 20px;
  cursor: pointer;
  background-color: #f1f1f1;
  border: 1px solid #ccc;
  border-radius: 5px;
  color: #333;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  transition: background-color 0.3s;
}
.button-file-upload:hover {
  background-color: #e1e1e1;
}
.button-file-upload:active {
  background-color: #d1d1d1;
}

.input-file-upload {
  display: none;
}

