}
.parent-reader-container .reader-container.-additional-padding- .epub-container {
  margin: auto;
  min-width: 200px;
  max-width: 1500px;
  pointer-events: initial;
  overflow-x: hidden !important;
}
.parent-reader-container .reader-container.-additional-padding- .epub-container::-webkit-scrollbar {
  width: 7px;
}
.parent-reader-container .reader-container.-additional-padding- .epub-container::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.parent-reader-container .reader-container.-additional-padding- .epub-container::-webkit-scrollbar-thumb {
  background: #888;
}
.parent-reader-container .reader-container.-additional-padding- .epub-container::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.parent-reader-container .reader-container.-target-child .epub-container::-webkit-scrollbar-track {
